.results-page {
  min-height: 100vh; 
  display: flex;
  flex-direction: column;
  padding: 0 20px 0 20px;
  box-sizing: border-box;
  background-color: #111;
  color: #fff; /* cor de fundo opcional */
}

.results-page h1 {
  margin-bottom: 20px;
  text-align: center;
}

.results-table-container {
  max-height: 500px;
  flex: 1;
  overflow: auto; 
  background: #1b1b1b;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  height: 100px;
}

.results-table-container::-webkit-scrollbar {
  width: 8px; /* largura da scrollbar */
}

.results-table-container::-webkit-scrollbar-thumb {
  background-color: #888; /* cor da parte "rol치vel" */
  border-radius: 4px; /* borda arredondada */
}

.results-table-container::-webkit-scrollbar-thumb:hover {
  background-color: #555; /* cor quando passa o mouse por cima */
}

.results-table-container::-webkit-scrollbar-track {
  background: #f1f1f1; /* cor de fundo da 치rea da scrollbar */
}

.results-table {
  width: 100%;
  max-height: 90%;
  border-collapse: collapse;
  border: 1px solid #ddd;
  border-radius: 24px;
}

.results-table tr:hover {
  background-color: #363535;
}

.results-table th,
.results-table td {
  padding: 12px;
  text-align: center;
  border-bottom: 1px solid #ddd;
  
}

.results-table th {
  background-color: #4caf50;
  font-weight: bold;
  position: sticky;
  top: 0;
  z-index: 1;
}

.button-container {
  display: flex;
  justify-content: center; 
}

.view-home-button {
  display: flex;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 18px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s;
  justify-self: center;
}

.view-results-button:hover {
  background-color: #45a049;
}

.word-count-select {
  background-color: #4CAF50;
  color: white;
  border: none;
  font-weight: bold;
  padding: 5px;
  font-size: 24px;
  width: 100%;
  cursor: pointer;
  appearance: none;
  text-align: center;
  text-align-last: center;
}

.word-count-select option {
  background-color: white;
  color: black;
}